export interface Collection {
  id: string
  name: string
  description: string
}

export const collections: Collection[] = [
  {
    id: "camisas",
    name: "Camisas",
    description: "Camisas urbanas y modernas"
  },
  {
    id: "hoodies",
    name: "Hoodies",
    description: "Sudaderas con capucha de alta calidad"
  },
  {
    id: "gorras",
    name: "Gorras",
    description: "Gorras y accesorios street style"
  }
]

export const getCollectionByName = (name: string) => 
  collections.find(collection => collection.name === name)
